  takePicture() {
    // tslint:disable-next-line:prefer-const
    let that = this;
    (navigator as any).camera.getPicture(function onSuccess(data) {
      that.show('...');
      that.uploadImage(data);
    }, function onError() {
        alert('');
      }, {
          quality: 100,
          destinationType: (window as any).Camera.DestinationType.FILE_URI, // FILE_URI
          sourceType: (window as any).Camera.PictureSourceType.CAMERA, // 
          cameraDirection: (window as any).Camera.Direction.FRONT, // --
          encodingType: (window as any).Camera.EncodingType.JPEG,
          targetWidth: 500,
          targetHeight: 500
      // encodingType: Camera.EncodingType.PNG //pngjpeg
         });
}

  openGallery() {
    // tslint:disable-next-line:prefer-const
    let that = this;
    // tslint:disable-next-line:only-arrow-functions
    (navigator as any).camera.getPicture(function(data) {
      that.show('...');
      that.uploadImage(data);
      // tslint:disable-next-line:prefer-const
    // tslint:disable-next-line:only-arrow-functions
    }, function(err) {
      alert('');
    }, {
      quality: 100,
      destinationType: (window as any).Camera.DestinationType.FILE_URI, // Base64
      sourceType: (window as any).Camera.PictureSourceType.SAVEDPHOTOALBUM, // 
      encodingType: (window as any).Camera.EncodingType.JPEG, // pngjpeg
    });
  }

  // 
  uploadImage(imagePath: any) {
    // tslint:disable-next-line:prefer-const
    let that = this;
    const options = {
      fileKey: 'img',
      httpMethod: 'POST',
      mimeType: 'image/jpeg',
      params: {}
    };
    // 
    this.fileTransfer.upload(imagePath, this.getIdCardInfoUrl, options).then(data => {
      // tslint:disable-next-line:prefer-const
      let res = JSON.parse(data.response);
      that.hide();
      if (res.status === 0) {
        that.presentAlert('');
        return;
      }
      that.username = res.msg.split('_')[0];
      that.idCard = res.msg.split('_')[1];
      that.changeDetectorRef.markForCheck();
      that.changeDetectorRef.detectChanges();
    }, err => {
      that.hide();
      that.presentAlert('');
    });
  }

  async show(text) {
    this.loading = await this.loadingCtrl.create({
      message: text
    });
    await this.loading.present();
  }

  hide() {
    if (this.loading) {
      this.loading.dismiss();
    }
  }

